<template>
	<div class="toolbar">
		<b-container>
			<b-row>
			  <b-col md="10">
			    <div class="filter-switch" :class="filterSlide">
			      Allergen Filter &nbsp;
		      	<toggle-button
		      		@change="toggleFilter($event.value)"
							:speed="200"
							:color="{checked: '#007bff', unchecked: '#78909C'}"
							:labels="{checked: 'ON', unchecked: 'OFF'}"
							:height="26"
							:width="60"/>
		  		</div>
			    <div class="filter-form" :class="filterOpenClass">
			    	<div class="filter-form-group" :class="filterOpenClass">
					    <multiselect
					      v-model="filters.selected"
					      :options="filters.options"
					      :multiple="true"
					      :hide-selected="true"
					      :clear-on-select="false"
					      track-by="label"
					      @input="filter"
					      :custom-label="customLabel"
					      placeholder="Add Dietary Filter">
					    </multiselect>
			    	</div>
			    </div>
			  </b-col>
			</b-row>
		</b-container>
	</div>
</template>

<script>
  import { eventService } from "../main";

	let options = [{
						label: 'Avoid Alcohol',
						value: 'alcohol'
					},
					{
						label: 'Avoid Artificial Coloring',
						value: 'artificial_coloring'
					},
					{
						label: 'Avoid Corn',
						value: 'corn'
					},
					{
						label: 'Avoid Eggs',
						value: 'eggs'
					},
					{
						label: 'Avoid Fish',
						value: 'fish'
					},
					{
						label: 'Avoid Garlic',
						value: 'garlic'
					},
					{
						label: 'Avoid Gluten',
						value: 'gluten'
					},
					{
						label: 'Avoid Milk',
						value: 'milk'
					},
					{
						label: 'Avoid Msg',
						value: 'msg'
					},
					{
						label: 'Avoid Nitrates',
						value: 'nitrates'
					},
					{
						label: 'Avoid Onion',
						value: 'onion'
					},
					{
						label: 'Avoid Peanuts',
						value: 'peanuts'
					},
					{
						label: 'Avoid Seeds',
						value: 'seeds'
					},
					{
						label: 'Avoid Shellfish',
						value: 'shellfish'
					},
					{
						label: 'Avoid Soy',
						value: 'soy'
					},
					{
						label: 'Avoid Meat',
						value: 'meat'
					},
					{
						label: 'Avoid Animal Products',
						value: 'animal_products'
					},
					{
						label: 'Avoid Sulfites',
						value: 'sulfites'
					},
					{
						label: 'Avoid Tree Nuts',
						value: 'tree_nuts'
					},
					{
						label: 'Avoid Wheat',
						value: 'wheat'
					},
					{
						label: 'Avoid Yeast',
						value: 'yeast'
					},
					{
						label: 'Avoid Crustacean Shellfish',
						value: 'crustacean_shellfish'
					},
					{
						label: 'Avoid Molluscan Shellfish',
						value: 'molluscan_shellfish'
					},
					{
						label: 'Avoid Cross Contact',
						value: 'cross_contact'
					}];

	export default {
		data() {
			return {
        isDataLoaded: false,
				isOpen: false,
				filters: {
					selected: [],
					options: options
				}
			}
		},
    created() {
      eventService.$on('onDataLoad', () => {
        this.isDataLoaded = true;
      })
    },
		methods: {
			toggleFilter(toggleState) {
				this.isOpen = toggleState;
				if(!this.isOpen) {
					this.resetFilter();
				}
			},
			filter() {
				let filters = [];
				if(this.filters.selected) {
					this.filters.selected.forEach(function(filter){
						filters.push(filter.value);
					});
				}
			  eventService.$emit('onSelectFilters', filters);
			},
			customLabel(option) {
				return `${option.label}`
			},
			resetFilter() {
				this.filters.selected = [];
				this.filter();
			}
		},
		computed: {
			filterOpenClass() {
				return {
					'open': this.isOpen
				}
			},
      filterSlide() {
        return {
          'animate' : this.isDataLoaded
        }
      }
		}
	}
</script>

<style lang="scss">
	.toolbar {
		background-color: #64B5F6;
		margin-bottom: 30px;
		padding: 5px 0 10px 0;
		color: #fff;
		@media(min-width: 768px) {
			padding: 10px 0;
		}
	}
	.filter-form-group {
		margin: -43px 0 0 0;
		transition: all 300ms;
		opacity: 0;
    @media(min-width: 768px) {
      margin-top: -30px;
    }
		input {
			border-color: none;
		}
		&.open {
			margin-top: 0;
			opacity: 1;
		}
		@media(min-width: 768px) {
			margin-left: 200px;
		}
	}
	.vue-js-switch {
		margin-bottom: 1px;
	}
	.filter-switch {
		padding: 7px 0;
    transition: all 600ms;
    opacity: 0;
    margin-left: 30px;
    &.animate {
      opacity: 1;
      margin-left: 0;
    }
		@media(min-width: 768px) {
			float: left;
		}
	}
</style>



