<template>
	<b-modal id="label" :title="item.name">
		Nutrition Label here...
	</b-modal>
</template>
<script>
  import { eventService } from "../../main";

	export default {
		data() {
      return {
        item: {}
      }
    },
    created() {
      eventService.$on('onItemClick', (item) => {
        this.item = item;
      });
    }
	}
</script>



